<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/productionSubject' }"
          ></el-breadcrumb-item
        >
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="box">
      <el-form label-width="100px" ref="dataForm" :model="data" :rules="rules" v-loading="dataloading">
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              <el-input v-model="this.oldDate" disabled></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              <el-input v-model="data.specimen"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              <el-input v-model="data.checkItem"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              <el-input v-model="data.checkResult"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              <el-input v-model="data.determine"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              <el-input v-model="data.checkStandard"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              <el-input v-model="data.checkOrganization"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item>
          <el-button type="success" @click="onSubmit('dataForm')" plain></el-button>
          <el-button type="danger" @click="goBack" plain></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import Request from "../../../../../services/api/request.js";
import { Urls } from "../../../../../services/constants";
import Auth from "../../../../../services/authentication/auth";

export default {
  name: "detailThirdPartySampling",
  data() {
    return {
      id : -1,
      oldDate: null,
      data: {
        specimen:"",
        checkItem:"", 
        checkResult:"", 
        determine:"", 
        checkStandard:"", 
        checkOrganization:"", 
      },     
      dataloading: true,
      rules: {
        createTime: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        specimen: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        checkItem: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        checkResult: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        determine: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        checkStandard: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        checkOrganization: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
      },
    };
  },
  created() {       
    this.getData(this.$route.query.checkId);   
  },
  methods: {    
    getData(id) {      
      this.dataloading = true;
      Request()
        .get("/api/product_check_record/get/" + id)        
        .then(response => {
          this.data = response;
          this.oldDate = this.data.createTime.slice(0,10);
          setTimeout(()=>{ this.dataloading = false }, 0.01*1000);
        })
        .catch(error => {
          console.log(error);
        });
    },

    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {          
          Request()
            .put("/api/product_check_record/update/" + this.data.id, {
              "checkItem": this.data.checkItem,
              "checkOrganization": this.data.checkOrganization,
              "checkResult": this.data.checkResult,
              "checkStandard":this.data.checkStandard,
              "createTime": this.data.createTime,
              "createUserId": this.data.createUserId,
              "determine": this.data.determine,
              "id": this.data.id,
              "productCheckTime": new Date().toJSON(),
              "productId": this.data.productId,
              "specimen": this.data.specimen,
              "updateTime": new Date().toJSON(),
              "updateUserId": Auth().user().attrs.id,
            })
            .then(response => {
              //this.$router.push({ path: "/threeProductsCertification" });
              this.$router.go(-1);
            })
            .catch(error => {});
        } else {
          console.log(" !!");
          return false;
        }
      });
    },
    
    goBack() {
      this.$router.go(-1);
    }  
  }
};
</script>

<style scoped>
</style>
