<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="box">
      <div class="iptBox">
        <el-button
          type="primary"
          plain
          v-if="!companyId && (loggedinUserType === 2 || loggedinUserType === 0)"
          v-on:click="$router.push(`/regulatoryRecord/create`)"
        ></el-button>
        <div v-else-if="companyId" class="fixed-value margin-right-20">
          <p v-if="filterCompanyName(companyId)">{{ filterCompanyName(companyId) }}</p>
          <p v-else></p>
        </div>
        <el-button type="primary" v-on:click="$router.push(`/commonWords`)" plain></el-button>
        <el-button type="primary" plain></el-button>
        <el-button type="primary" plain></el-button>
        <el-button type="primary" plain v-if="companyId" @click="$router.go(-1)"></el-button>
        <div class="special-container" style="margin-left:auto">
          <el-button
            type="success"
            icon="el-icon-plus"
            v-if="isShowCheckbox != 0"
            plain
            @click="actionConfirm(1)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-minus"
            v-if="isShowCheckbox != 0"
            plain
            @click="actionConfirm(0)"
            style="margin-right:10px"
          >1</el-button>
          <el-checkbox
            v-model="isShowCheckbox"
            true-label="1"
            false-label="0"
            @change="showCheckbox"
          >1:</el-checkbox>
        </div>
      </div>
      <el-dialog :visible.sync="alert_dialogVisible" width="30%" modal>
        <span>
          <i class="el-icon-warning">&nbsp; !!!</i>
        </span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="alert_dialogVisible = false" type="primary" plain></el-button>
        </span>
      </el-dialog>
      <el-dialog :visible.sync="confirm_dialogVisible" width="30%" modal>
        <span>
          <i class="el-icon-warning">&nbsp;</i>
        </span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="confirm_dialogVisible = false" type="primary" plain></el-button>
          <el-button :type="btnColor" @click="updateSelectedRows()" plain></el-button>
        </span>
      </el-dialog>

      <el-container>
        <el-table
          :data="tableData"
          style="width: 100%"
          :row-class-name="rowIndex"
          v-loading="listLoading"
          highlight-current-row
        >
          <el-table-column label width="35" v-if="isShowCheckbox != 0">
            <template slot-scope="{ row }">
              <el-checkbox style="margin-left:auto" @change="changeCheckStatus(row.id)"></el-checkbox>
            </template>
          </el-table-column>
          <el-table-column :formatter="order" label="" width="70"></el-table-column>
          <el-table-column prop="createTime" label="" width="100">
            <template slot-scope="{ row }">
              {{
              row.createTime | formatDate
              }}
            </template>
          </el-table-column>
          <el-table-column prop="township" label="" width="70">
            <template slot-scope="{ row }">
              {{
              filterTownship(row.townId)
              }}
            </template>
          </el-table-column>
          <el-table-column prop="unitinspec" label="" class-name="padding-left-20">
            <template slot-scope="{ row }">
              {{
              filterCompanyName(row.companyId)
              }}
            </template>
          </el-table-column>
          <el-table-column prop="inspector" label=""></el-table-column>
          <el-table-column prop="conclusion" label="" width="70">
            <template slot-scope="{ row }">{{ row.conclusion == 1 ? "" : "" }}</template>
          </el-table-column>
          <el-table-column prop="otherProblems" label="" width="80"></el-table-column>
          <el-table-column prop="scenePhotos" label="" width="70" class-name="text-center">
            <template slot-scope="{ row }">{{ getCountElement(row.scenePhotos, row.sign) }}</template>
          </el-table-column>
          <el-table-column prop="sign" label="" width="70" class-name="text-center">
            <template slot-scope="{ row }">{{ checkSign(row.sign) }}</template>
            <!-- <template slot-scope="{ row }">{{ getCountElement(row.sign) }}</template> -->
          </el-table-column>
          <el-table-column label="" class-name="text-center">
            <template slot-scope="{ row }">
              <el-button
                type="success"
                plain
                v-on:click="
                  $router.push({
                    path: `/regulatoryRecord/${row.id}`,
                    query: {
                      company: filterCompanyName(row.companyId),
                      township: filterTownship(row.townId)
                    }
                  })
                "
              ></el-button>
              <el-button
                type="warning"
                v-on:click="$router.push(`/regulatoryRecord/edit/${row.id}`)"
                plain
              ></el-button>
            </template>
          </el-table-column>
          <el-table-column prop="yield" label="1" align="center" width="100">
            <template slot-scope="{ row }">
              <i class="el-icon-check" v-if="row.specialFlag"></i>
            </template>
          </el-table-column>
        </el-table>
      </el-container>

      <div class="pageBox">
        <pagination
          v-show="total > 0"
          :total="total"
          :page.sync="page.pageIndex"
          :limit.sync="page.pageSize"
          @pagination="getData"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from "@/components/common/pagination";
import Request from "../../services/api/request.js";
import Auth from "@/services/authentication/auth.js";
export default {
  name: "regulatoryRecord",
  components: { Pagination },
  data() {
    return {
      loggedinUserType: null,
      page: {
        pageIndex: 1,
        pageSize: 20
      },
      total: 0,
      tableData: [],
      listLoading: false,
      companyList: [],
      companyId: 0,
      township: [],
      isShowCheckbox: 0,
      selectedRows: [],
      alert_dialogVisible: false,
      confirm_dialogVisible: false,
      btnColor: "",
      action: ""
    };
  },
  created() {
    this.companyId = this.$route.query.companyId;
    this.getData();
    this.getTown();
    this.getCompanyProduct();
    this.loggedinUserType = Auth().user().attrs.userType;
  },
  methods: {
    //
    getData() {
      this.listLoading = true;
      Request()
        .get("/api/supervision_record/all", {
          companyId: this.companyId,
          pageNo: this.page.pageIndex - 1,
          pageSize: this.page.pageSize
        })
        .then(response => {
          this.tableData = response.data;
          this.total = response.total;
          setTimeout(() => {
            this.listLoading = false;
          }, 0.5 * 1000);
        })
        .catch(error => {
          console.log(error);
        });
    },
    getTown() {
      Request()
        .get("/api/town/all")
        .then(response => {
          this.township = this.township.concat(response);
        })
        .catch(error => {
          console.log(error);
        });
    },
    getCompanyProduct() {
      Request()
        .get("/api/company_production/name")
        .then(response => {
          this.companyList = this.companyList.concat(response);
        })
        .catch(error => {
          console.log(error);
        });
    },
    filterTownship(townId) {
      let town = this.township.find(x => x.id === townId);
      if (town) {
        return town.name;
      } else {
        return "";
      }
    },
    filterCompanyName(companyId) {
      let company = this.companyList.find(x => x.companyId === companyId);
      if (company) {
        return company.companyName;
      } else {
        return "";
      }
    },
    rowIndex({ row, rowIndex }) {
      row.rowIndex = rowIndex;
    },
    order(row) {
      return this.page.pageSize * (this.page.pageIndex - 1) + row.rowIndex + 1;
    },
    getCountElement(val1, val2) {
      if (!val1 && !val2) return 0;
      else return val1.split(",").length + val2.split(",").length;
    },
    checkSign(val) {
      if (!val) return "";
      else return "";
    },
    changeCheckStatus(id) {
      let index = this.selectedRows.indexOf(id);
      if (index > -1) this.selectedRows.splice(index, 1);
      if (event.target.checked) {
        this.selectedRows.push(id);
      }
    },
    actionConfirm(action) {
      this.action = action;
      if (!this.selectedRows.length) {
        this.alert_dialogVisible = true;
      } else {
        this.confirm_dialogVisible = true;
        this.btnColor = action > 0 ? "success" : "danger";
      }
    },
    updateSelectedRows() {
      for (let index in this.selectedRows) {
        this.confirm_dialogVisible = false;
        this.listLoading = true;
        Request()
          .put(
            "/api/supervision_record/update_special_flag/" +
              this.selectedRows[index] +
              "?specialFlag=" +
              this.action
          )
          .then(response => {
            setTimeout(() => {
              this.listLoading = false;
            }, 0.5 * 1000);
            this.selectedRows = [];
            this.isShowCheckbox = 0;
            this.getData();
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    showCheckbox() {
      if (!event.target.checked) {
        this.selectedRows = [];
      }
    }
  }
};
</script>

<style lang="scss">
@import "./regulatoryRecord.scss";
</style>