<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="box">
      <div class="iptBox">
        <el-row class="w-100">
          <el-col :span="3">
            <el-button
              type="primary"
              v-on:click="$router.push(`/productionStandard/create`)"
              plain
              v-if="loggedinUserType === 1 || loggedinUserType === 0"
            ></el-button>
          </el-col>
          <el-col :span="6">
            <div class="select_label no-margin-left"></div>
            <el-input v-model="productName" @change="getList()" class="w-80"></el-input>
          </el-col>
          <el-col :span="6">
            <div class="select_label"></div>
            <el-select v-model="category" @change="getList()">
              <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-col>
        </el-row>
      </div>

      <el-container>
        <el-table
          :data="tableData"
          v-loading="listLoading"
          style="width: 100%"
          :row-class-name="rowIndex"
          highlight-current-row
        >
          <el-table-column :formatter="order" label="" width="100"></el-table-column>
          <el-table-column prop="productName" label=""></el-table-column>
          <el-table-column prop="category" label="">
            <template slot-scope="{ row }">{{ filterCategory(row.category) }}</template>
          </el-table-column>
          <el-table-column prop="releaseTime" label="" class-name="text-center">
            <template slot-scope="{ row }">{{ row.releaseTime | formatDate }}</template>
          </el-table-column>
          <el-table-column prop="releasePerson" label=""></el-table-column>
          <el-table-column label="" class-name="text-center">
            <template slot-scope="{ row }">
              <el-button
                type="success"
                plain
                v-if="loggedinUserType !== 1"
                v-on:click="
                  $router.push({
                    path: `/productionStandard/detail/`+ row.id
                  })
                "
              ></el-button>
              <el-button
                type="warning"
                plain
                v-if="loggedinUserType === 1 || loggedinUserType === 0"
                v-on:click="
                  $router.push({
                    path: `/productionStandard/edit/`+ row.id
                  })
                "
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-container>
      <div class="pageBox">
        <pagination
          v-show="total > 0"
          :total="total"
          :page.sync="page.pageIndex"
          :limit.sync="page.pageSize"
          @pagination="getList"
        />
      </div>
    </div>
  </div>
</template>
<script>
import Pagination from "@/components/common/pagination";
import Request from "../../services/api/request.js";
import Auth from "@/services/authentication/auth.js";

export default {
  name: "productStandard",
  components: { Pagination },
  data() {
    return {
      releaseTimeFrom: "",
      releaseTimeTo: "",
      page: {
        pageIndex: 1,
        pageSize: 20
      },
      listLoading: true,
      total: 0,
      tableData: [],
      productName: "",
      category: 0,
      loggedinUserType: null,
      options: [
        { id: 0, name: "" },
        { id: 1, name: "" },
        { id: 2, name: "" },
        { id: 3, name: "" }
      ]
    };
  },
  created() {
    this.getList();
    this.loggedinUserType = Auth().user().attrs.userType;
  },
  methods: {
    getList() {
      this.listLoading = true;
      Request()
        .get("/api/production_standard/all", {
          category: this.category,
          productName: this.productName,
          pageNo: this.page.pageIndex - 1,
          pageSize: this.page.pageSize
        })
        .then(response => {
          this.tableData = response.data;
          this.total = response.total;
          setTimeout(() => {
            this.listLoading = false;
          }, 0.5 * 1000);
        })
        .catch(error => {
          console.log(error);
        });
    },
    rowIndex({ row, rowIndex }) {
      row.rowIndex = rowIndex;
    },
    order(row) {
      return this.page.pageSize * (this.page.pageIndex - 1) + row.rowIndex + 1;
    },
    filterCategory(id) {
      let category = this.options.find(x => x.id === parseInt(id));
      if (category) {
        return category.name;
      } else {
        return "";
      }
    }
  }
};
</script>

<style scoped>
</style>