  <template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="box">
      <div class="iptBox">
        <el-button type="primary" plain v-on:click="handleImportExcel()"></el-button>
        <el-button type="primary" plain v-on:click="handleDownloadSample()"></el-button>
        <input
          type="file"
          id="file"
          ref="file"
          accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
          v-on:change="handleFileUpload()"
          name="Upload Excel"
          style="display:none"
        />
      </div>
      <el-container>
        <el-table
          :data="tableData"
          style="width: 100%"
          :row-class-name="rowIndex"
          v-loading="listLoading"
          highlight-current-row
        >
          <el-table-column :formatter="order" label=""></el-table-column>
          <el-table-column prop="sampleName" label=""></el-table-column>
          <el-table-column prop="sampleScientificName" label=""></el-table-column>
          <el-table-column prop="sampleSerialNum" label=""></el-table-column>
          <el-table-column prop="city" label=""></el-table-column>
          <el-table-column prop="district" label=""></el-table-column>
          <el-table-column prop="inspectedUnit" label=""></el-table-column>
          <el-table-column prop="sampleType" label=""></el-table-column>
          <el-table-column prop="furaMeta" label=""></el-table-column>
          <el-table-column prop="malaGreen" label=""></el-table-column>
          <el-table-column prop="detectionRoomNum" label=""></el-table-column>
          <el-table-column prop="detectionRoomName" label=""></el-table-column>
          <el-table-column prop="detectionTime" label=""></el-table-column>
        </el-table>
      </el-container>
      <div class="pageBox">
        <pagination
          v-show="total > 0"
          :total="total"
          :page.sync="page.pageIndex"
          :limit.sync="page.pageSize"
          @pagination="getList"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from "@/components/common/pagination";
import Request from "../../services/api/request.js";
import Auth from "@/services/authentication/auth.js";
import { Urls } from "@/services/constants";
import axios from "axios";
import XLSX from "xlsx";

export default {
  name: "aquaticStatistics",
  components: { Pagination },
  data() {
    return {
      page: {
        pageIndex: 1,
        pageSize: 20
      },
      file: null,
      total: 0,
      tableData: [],
      listLoading: false,
      filesample:"AquaticSample.xlsx",
    };
  },

  created() {
    this.getList();
  },

  methods: {
    getList() {
      this.listLoading = true;
      Request()
        .get("/api/aquatic_statistics/all", {
          pageNo: this.page.pageIndex - 1,
          pageSize: this.page.pageSize
        })
        .then(response => {
          this.tableData = response.data;
          this.total = response.total;
          setTimeout(() => {
            this.listLoading = false;
          }, 0.5 * 1000);
        })
        .catch(error => {
          console.log(error);
        });
    },

    handleDownloadSample() {
      axios({
        url: Urls.DOWNLOAD_URL() + this.filesample,
        method: "GET",
        responseType: "blob" // important
      }).then(response => {
        const url = window.URL.createObjectURL(response.data)
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', 'file.png') //or any other extension
        document.body.appendChild(link)
        link.click()
      }).catch(()=> console.log('error occured'))
    },

    fixdata(data) {
      var o = "",
        l = 0,
        w = 10240;
      for (; l < data.byteLength / w; ++l)
        o += String.fromCharCode.apply(
          null,
          new Uint8Array(data.slice(l * w, l * w + w))
        );
      o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)));
      return o;
    },

    workbook_make_json(workbook) {
      var key = {
        : "district",
        : "furaMeta",
        : "malaGreen",
        : "city",
        : "sampleName",
        : "sampleScientificName",
        : "sampleType",
        : "sampleSerialNum",
        : "detectionRoomName",
        : "detectionRoomNum",
        : "detectionTime",
        : "inspectedUnit"
      };
      var result = [];
      var index = 0;
      workbook.SheetNames.forEach(function(sheetName) {
        var roa = XLSX.utils.sheet_to_row_object_array(
          workbook.Sheets[sheetName]
        );

        if (roa.length > 0) {
          roa.forEach(function(row) {
            let changeVal = Object.keys(row).map(k => {
              let v = key[k] || k;
              return { [v]: row[k] };
            });

            result.push(
              Object.assign(
                {},
                ...changeVal,
                { createTime: new Date().toJSON() },
                { createUserId: Auth().user().attrs.id },
                { id: 0 },
                { updateTime: new Date().toJSON() },
                { updateUserId: Auth().user().attrs.id }
              )
            );
          });
        }
      });

      return result;
    },

    handleImportExcel() {
      document.getElementById("file").click();
    },

    handleFileUpload() {
      this.file = this.$refs.file.files[0];
      var reader = new FileReader();
      var obj = this;

      reader.onload = function(e) {
        let fixedData = obj.fixdata(e.target.result),
          workbook = XLSX.read(btoa(fixedData), { type: "base64" });
        let val = obj.workbook_make_json(workbook);

        Request()
          .post("/api/aquatic_statistics/create", val)
          .then(response => {
            obj.getList();
          })
          .catch(error => {
            console.log(error);
          });
      };

      reader.readAsArrayBuffer(this.file);
    },

    rowIndex({ row, rowIndex }) {
      row.rowIndex = rowIndex;
    },

    order(row) {
      return this.page.pageSize * (this.page.pageIndex - 1) + row.rowIndex + 1;
    }
  }
};
</script>