<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/productionSubject' }"
          ></el-breadcrumb-item
        >
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="box">
      <el-form
        ref="ruleForm"
        :model="ruleFormValue"
        :rules="rules"
        label-width="100px"
        v-loading="listLoading"
      >
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="propertyName">
              <el-input v-model="ruleFormValue.propertyName" ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="propertyOptions">
              <el-input v-model="ruleFormValue.propertyOptions" ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="propertySort">
              <el-input v-model="ruleFormValue.propertySort" ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item>
          <el-button type="success" plain
          @click="$router.push({
            path: `/productionSubject/mainProduct/productProperty/editProductProperty/customProductProperty/${ruleFormValue.productId}`,
            query: {
              id: ruleFormValue.id
            }
          })"
          ></el-button>
          <el-button type="success" @click="onSubmit('ruleForm')" plain></el-button>
          <el-button type="danger" @click="goBack" plain></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import Request from "../../../../../services/api/request.js";
export default {
  name: "editProductProperty",
  data() {
    return {
      listLoading: false,
      ruleFormValue: {
        propertyName: "",
        propertyOptions: "",
        propertySort: "",
        productId: 0,
        id: -1
      },
      rules: {
        propertyName: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        propertyOptions: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        propertySort: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      }
    };
  },
  created() {    
    this.ruleFormValue.productId = this.$route.params.id;
    this.ruleFormValue.id = this.$route.query.id;
    this.getProductPropertyInfo();
  },
  methods: {
    getProductPropertyInfo() {
      this.listLoading = true;
      Request()
        .get("/api/product_property/get/" + this.ruleFormValue.id)
        .then(response => {
          setTimeout(() => {
            this.listLoading = false;          
          }, 0.5*100);
          this.ruleFormValue.propertyName = response.propertyName;
          this.ruleFormValue.propertyOptions = response.propertyOptions;
          this.ruleFormValue.propertySort = response.propertySort;
        })
        .catch(error => {});
    },
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var formData = new FormData();
          formData.append("productId", this.ruleFormValue.productId);
          formData.append("propertyName", this.ruleFormValue.propertyName);
          formData.append("propertyOptions", this.ruleFormValue.propertyOptions);
          formData.append("propertySort", this.propertySort);
          this.listLoading = true;
          Request()
            .put("/api/product_property/update/" + this.ruleFormValue.id, formData)
            .then(response => {
              setTimeout(() => {
                this.listLoading = false;          
              }, 0.5*100);
              this.$router.push({
                path: `/productionSubject/mainProduct/productProperty/${this.ruleFormValue.productId}`
              });
            })
            .catch(error => {});
        } else {
          console.log(" !!");
          return false;
        }
      });
    },
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped>
</style>
