<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="box">
      <div class="iptBox">
        <el-button type="primary" v-on:click="$router.push(`/jobDefinition/create`)" plain></el-button>
      </div>

      <el-container>
        <el-table
          :data="tableData"
          v-loading="listLoading"
          style="width: 100%"
          :row-class-name="rowIndex"
          highlight-current-row
        >
          <el-table-column :formatter="order" label="" width="180"></el-table-column>
          <el-table-column prop="jobName" label=""></el-table-column>
          <el-table-column prop="jobType" label="">
            <template slot-scope="{ row }">{{ getJobType(row.jobType) }}</template>
          </el-table-column>
          <el-table-column label="" class-name="text-center">
            <template slot-scope="{ row }">
              <el-button
                type="success"
                plain
                v-on:click="
                                    $router.push({
                                        path: `/jobDefinition/${row.id}`,
                                        query: {
                                            jobName: row.jobName,
                                            jobType: getJobType(row.jobType)
                                        }
                                    })"
              ></el-button>
              <el-button type="danger" v-on:click="handleDelete(`${row.id}`)" plain></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-container>
      <div class="pageBox">
        <Pagination
          v-show="total > 0"
          :total="total"
          :page.sync="page.pageIndex"
          :limit.sync="page.pageSize"
          @pagination="getList"
        />
      </div>
    </div>
  </div>
</template>
<script>
import Pagination from "@/components/common/pagination";
import Request from "../../services/api/request.js";

export default {
  name: "jobDefinition",
  components: { Pagination },
  data() {
    return {
      jobName: "",
      jobType: 0,
      page: {
        pageIndex: 1,
        pageSize: 20
      },
      listLoading: true,
      total: 0,
      tableData: [],
      jobTypes: [
        { id: 0, name: "" },
        { id: 1, name: "" },
        { id: 2, name: "" }
      ]
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    order(row) {
      return this.page.pageSize * (this.page.pageIndex - 1) + row.rowIndex + 1;
    },
    rowIndex({ row, rowIndex }) {
      row.rowIndex = rowIndex;
    },
    getList() {
      this.listLoading = true;
      Request()
        .get("/api/job_definition/all", {
          pageNo: this.page.pageIndex - 1,
          pageSize: this.page.pageSize
        })
        .then(response => {
          this.tableData = response.data;
          this.total = response.total;
          setTimeout(() => {
            this.listLoading = false;
          }, 0.5 * 1000);
        })
        .catch(error => {
          console.log(error);
        });
    },
    getJobType(id) {
      let type = this.jobTypes.find(x => x.id === id);
      if (type) {
        return type.name;
      } else {
        return "";
      }
    },
    handleDelete(id) {
      this.$confirm("?", "", {
        type: "warning"
      }).then(() => {
        this.listLoading = true;
        Request()
          .delete("/api/job_definition/delete/" + id)
          .then(response => {
            this.getList();
            setTimeout(() => {
              this.listLoading = false;
            }, 0.5 * 1000);
          })
          .catch(error => {
            console.log(error);
          });
      });
    }
  }
};
</script>

<style lang="scss">
@import "./jobDefinition.scss";
</style>