<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/productionSubject' }"
          ></el-breadcrumb-item
        >
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="box">
      <el-form ref="ruleForm" :model="ruleFormValue" :rules="rules" label-width="100px" v-loading="listLoading">
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="createTime">
              <el-input v-model="ruleFormValue.createTime"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="specimen">
              <el-input v-model="ruleFormValue.specimen"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="checkItem">
              <el-input v-model="ruleFormValue.checkItem"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="checkResult">
              <el-input v-model="ruleFormValue.checkResult"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="determine">
              <el-input v-model="ruleFormValue.determine"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="checkStandard">
              <el-input v-model="ruleFormValue.checkStandard"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="checkOrganization">
              <el-input v-model="ruleFormValue.checkOrganization"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item>
          <el-button type="success" @click="onSubmit('ruleForm')" plain></el-button>
          <el-button type="danger" @click="goBack" plain></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import Request from "../../../../../services/api/request.js";
import { Urls } from "../../../../../services/constants";
import Auth from "../../../../../services/authentication/auth";

export default {
  name: "addThirdPartySampling",
  data() {    
    return {      
      productId: -1,
      ruleFormValue: {
        createTime: new Date().toISOString().slice(0,10),
        specimen: "",
        checkItem: "",
        checkResult: "",
        determine: "",
        checkStandard: "",
        checkOrganization: "",
        listLoading:true,
      },
      rules: {
        createTime: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        specimen: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        checkItem: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        checkResult: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        determine: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        checkStandard: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        checkOrganization: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],        
      },     
    };
  },
  created() {
    this.productId = this.$route.params.id;
    this.listLoading = false;
  },
  methods: {     
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.listLoading = true;
          Request()
            .post("/api/product_check_record/create", {
              "checkItem": this.ruleFormValue.checkItem,
              "checkOrganization": this.ruleFormValue.checkOrganization,
              "checkResult": this.ruleFormValue.checkResult,
              "checkStandard":this.ruleFormValue.checkStandard,
              "createTime": new Date().toJSON(),
              "createUserId": Auth().user().attrs.id,
              "determine": this.ruleFormValue.determine,
              "id": 0,
              "productCheckTime": new Date().toJSON(),
              "productId": this.productId,
              "specimen": this.ruleFormValue.specimen,
              "updateTime": new Date().toJSON(),
              "updateUserId": Auth().user().attrs.id,
            })
            .then(response => {
              //this.$router.push({ path: "/threeProductsCertification" });              
              this.$router.go(-1);
              setTimeout(() => { this.listLoading = false; }, 0.01 * 1000);
            })
            .catch(error => {});
        } else {
          console.log(" !!");
          return false;
        }
      });
    },
    
    goBack() {
      this.$router.go(-1);
    },
    
  }
};
</script>

<style scoped>
</style>
