<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }" class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="box">
      <el-container>
        <el-table :data="tableData" style="width: 100%" :row-class-name="rowIndex">
          <el-table-column :formatter="order" label="" width="100"></el-table-column>
          <el-table-column prop="name" label="" width="100"></el-table-column>
          <el-table-column prop="classification" label="" width="100"></el-table-column>
          <el-table-column prop="type" label=""></el-table-column>
          <el-table-column prop="bookNo" label=""></el-table-column>
          <el-table-column prop="date" label="" class-name="text-center"></el-table-column>
          <el-table-column prop="limitedPeriod" label="" class-name="text-center"></el-table-column>
          <el-table-column prop="yield" label=""></el-table-column>
          <el-table-column prop="yield" goProduct label="" width="250" class-name="text-center">
            <!-- <template slot-scope="scope"> -->
            <template>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button icon="el-icon-edit" circle @click="goEdit()" plain type="success"></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  icon="el-icon-warning-outline"
                  circle
                  @click="goProduct()"
                  plain
                  type="warning"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  icon="el-icon-office-building"
                  circle
                  @click="goStorage()"
                  plain
                  type="primary"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button icon="el-icon-close" circle plain type="danger"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </el-container>
      <div class="pageBox">
        <pagination
          v-show="total > 0"
          :total="total"
          :page.sync="page.pageIndex"
          :limit.sync="page.pageSize"
          @pagination="getList"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from "@/components/common/pagination";
import Request from "../../../services/api/request.js";
export default {
  components: { Pagination },
  data() {
    return {
      page: {
        pageIndex: 1,
        pageSize: 10
      },
      total: 0,
      options: [
        {
          value: "",
          label: ""
        }
      ],
      tableData: [
        {
          name: "",
          classification: "",
          type: "",
          bookNo: "123456",
          date: "2018-09-012020-05-01",
          limitedPeriod: "1",
          yield: "600"
        },
        {
          name: "",
          classification: "",
          type: "",
          bookNo: "123456",
          date: "2018-09-012020-05-01",
          limitedPeriod: "1",
          yield: "600"
        },
        {
          name: "",
          classification: "",
          type: "",
          bookNo: "123456",
          date: "2018-09-012020-05-01",
          limitedPeriod: "1",
          yield: "600"
        },
        {
          name: "",
          classification: "",
          type: "",
          bookNo: "123456",
          date: "2018-09-012020-05-01",
          limitedPeriod: "1",
          yield: "600"
        }
      ]
    };
  },
  methods: {
    rowIndex({ row, rowIndex }) {
      row.rowIndex = rowIndex;
    },
    order(row) {
      return this.page.pageSize * (this.page.pageIndex - 1) + row.rowIndex + 1;
    },
    //
    handleSizeChange(val) {
      let that = this;
      that.page.pageSize = val;
      that.fullPage(1);
    },
    //
    handleCurrentChange(val) {
      let that = this;
      that.page.pageIndex = val;
      that.fullPage(1);
    },
    //
    goEdit() {
      this.$router.push({ path: "/cp_editProduction" });
    },
    goProduct() {
      this.$router.push({ path: "/cp_businessProducts" });
    }
  }
};
</script>

