<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="box">
      <div class="iptBox">
        <div class="filter-item">
          <div class="select_label no-margin-left"></div>
          <el-select v-model="productType" placeholder="" @change="getList()">
            <el-option
              v-for="item in productTypes"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>

        <div class="filter-item">
          <div class="select_label"></div>
          <el-select v-model="companyScale" placeholder="" @change="getList()">
            <el-option
              v-for="item in companyScales"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
      </div>
      <el-container>
        <el-table 
          :data="tableData" 
          style="width: 100%" 
          :row-class-name="rowIndex"
          v-loading="listLoading"
        >
          <el-table-column :formatter="order" label="" width="70"></el-table-column>
          <el-table-column prop="companyName" label="" width="120"></el-table-column>
          <el-table-column prop="companyScale" label="" width="120">
            <template slot-scope="{ row }">{{ companyScales[row.companyScale].label }}</template>
          </el-table-column>
          <el-table-column prop="productType" label="" width="120">
            <template slot-scope="{ row }">{{ productTypes[row.productType].label }}</template>
          </el-table-column>
          <el-table-column prop="legalPerson" label="" width="150"></el-table-column>
          <el-table-column prop="companyAddress" label="" width="120"></el-table-column>
          <el-table-column prop="creditCode" label="" width="200"></el-table-column>
          <el-table-column prop="contactWay" label="" width="120"></el-table-column>
          <el-table-column prop="supervisionNature" label="" width="180">
            <template slot-scope="{ row }">{{ supervisions[row.supervisionNature] }}</template>
          </el-table-column>
          <el-table-column prop="operations" label="" width="250" class-name="text-center">
            <template slot-scope="{ row }">
              <el-button v-on:click="gotoDetailPage(row)" plain type="primary"></el-button>
              <el-button v-on:click="gotoProductBusinessPage(row)" plain type="success"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-container>
      <div class="pageBox">
        <pagination
          v-show="total > 0"
          :total="total"
          :page.sync="page.pageIndex"
          :limit.sync="page.pageSize"
          @pagination="getList"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from "@/components/common/pagination";
import Request from "@/services/api/request";
export default {
  name: "companyBusiness",
  components: { Pagination },
  data() {
    return {
      companyScales: [
        { value: 0, label: "" },
        { value: 1, label: "" },
        { value: 2, label: "" },
        { value: 3, label: "" }
      ],
      productTypes: [
        { value: 0, label: "" },
        { value: 1, label: "" },
        { value: 2, label: "" },
        { value: 3, label: "" },
        { value: 4, label: "" }
      ],
      supervisions: ["Unknown", "", ""],
      companyScale: 0,
      productType: 0,
      sortBy: "id",
      page: {
        pageIndex: 1,
        pageSize: 20
      },
      total: 0,
      options: [
        {
          value: "",
          label: ""
        }
      ],
      tableData: [],
      listLoading: false
    };
  },
  created() {
    this.getList();
  },
  methods: {
    gotoDetailPage(row) {
      this.$router.push(`/companyBusiness/detailsCompanyBusiness/${row.id}`);
    },
    gotoProductBusinessPage(row) {
      this.$router.push(`/companyBusiness/productBusiness/${row.id}`);
    },
    getList() {
      this.listLoading = true;
      Request()
        .get("/api/company_business/all", {
          company_scale: this.companyScale,
          product_type: this.productType,
          pageNo: this.page.pageIndex - 1,
          pageSize: this.page.pageSize,
          sortBy: this.sortBy
        })
        .then(response => {
          this.tableData = response.data;
          this.total = this.tableData.length;
          setTimeout(() => {
            this.listLoading = false;
          }, 0.5 * 1000);
        })
        .catch(error => {
          console.error(error);
        });
    },
    rowIndex({ row, rowIndex }) {
      row.rowIndex = rowIndex;
    },
    order(row) {
      return this.page.pageSize * (this.page.pageIndex - 1) + row.rowIndex + 1;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./companyBusiness.scss";
</style>
