<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="box">
      <div class="iptBox">
        <el-checkbox v-model="checked"></el-checkbox>
        <div class="select_label"></div>
        <el-input v-model="input" class="input-layout"></el-input>
        <div class="select_label"></div>
        <el-input v-model="input" class="input-layout"></el-input>
        <el-button type="primary" v-on:click="$router.push(`#`)" plain class="margin-left-40"></el-button>
      </div>

      <el-container>
        <el-table
          :data="tableData"
          v-loading="listLoading"
          style="width: 100%"
          :row-class-name="rowIndex"
          highlight-current-row
        >
          <el-table-column :formatter="order" label="" width="180"></el-table-column>
          <el-table-column prop="productName" label=""></el-table-column>
          <el-table-column prop="productionQuantity" label=""></el-table-column>
          <el-table-column prop="productionQuantity" label="">
            <div class="sub-title">50</div>
            <el-progress :percentage="50"></el-progress>
          </el-table-column>
          <el-table-column prop="productionQuantity" label=""></el-table-column>
          <el-table-column prop="productionQuantity" label=""></el-table-column>
          <el-table-column prop="productionQuantity" label="">
            <div class="sub-title">100</div>
            <el-progress :percentage="100"></el-progress>
          </el-table-column>
        </el-table>
      </el-container>
      <div class="pageBox">
        <Pagination
          v-show="total > 0"
          :total="total"
          :page.sync="page.pageIndex"
          :limit.sync="page.pageSize"
          @pagination="getList"
          layout="prev, pager, next, sizes, jumper"
        />
      </div>
    </div>
  </div>
</template>
<script>
import Pagination from "@/components/common/pagination";
import Request from "../../services/api/request.js";
export default {
  name: "statisticsFarmers",
  components: { Pagination },
  data() {
    return {
      page: {
        pageIndex: 1,
        pageSize: 20
      },
      total: 0,
      tableData: [],
      listLoading: false,
      confirm_dialogVisible: false,
      selectedId: null
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      this.listLoading = true;
      Request()
        .get("/api/production_record/all", {
          pageNo: this.page.pageIndex - 1,
          pageSize: this.page.pageSize
        })
        .then(response => {
          this.tableData = response.data;
          this.total = response.total;
          setTimeout(() => {
            this.listLoading = false;
          }, 0.5 * 1000);
        })
        .catch(error => {
          console.log(error);
        });
    },
    deleteRow() {
      Request()
        .delete("/api/production_record/delete/" + this.selectedId)
        .then(response => {
          setTimeout(() => {
            this.confirm_dialogVisible = false;
          }, 0.5 * 1000);
          this.getData();
        })
        .catch(error => {
          console.log(error);
        });
    },
    rowIndex({ row, rowIndex }) {
      row.rowIndex = rowIndex;
    },
    order(row) {
      return this.page.pageSize * (this.page.pageIndex - 1) + row.rowIndex + 1;
    },
    showConfirmDialog(id) {
      this.selectedId = id;
      this.confirm_dialogVisible = true;
    }
  }
};
</script>

<style scoped>
@import "./statisticsFarmers.scss";
</style>