<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="box">
      <el-form ref="ruleFormValue" :model="ruleFormValue" :rules="rules" label-width="100px" v-loading="listLoading">
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="productName">
              <el-input v-model="ruleFormValue.productName"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="atunitprice">
              <el-input v-model.number="ruleFormValue.atunitprice"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="grade">
              <el-input v-model="ruleFormValue.grade"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="productArea">
              <el-input v-model="ruleFormValue.productArea"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="variety">
              <el-input v-model="ruleFormValue.variety"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="" prop="specification">
              <el-input v-model="ruleFormValue.specification"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="productProfile">
              <el-input
                v-model="ruleFormValue.productProfile"
                ato-complete="off"
                type="textarea"
                :rows="10"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item>
          <el-button type="success" plain @click="onSubmit('ruleFormValue')"></el-button>
          <el-button type="danger" plain v-on:click="$router.go(-1)"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>


<script>
import Request from "@/services/api/request.js";
import Auth from "@/services/authentication/auth";
export default {
  name: "addProductBusiness",
  data() {
    return {
      companyId: -1,
      ruleFormValue: {
        productName: "",
        atunitprice: 0,
        grade: "",
        productArea: "",
        variety: "",
        specification: "",
        productProfile: "",
        listLoading: true,
      },
      rules: {
        productName: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        atunitprice: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        grade: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        productArea: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        variety: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        specification: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        productProfile: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      }
    };
  },
  created() {
    this.companyId = this.$route.params.id;
    this.listLoading = false;
  },
  methods: {
    onSubmit(formName) {      
      this.$refs[formName].validate(valid => {
        if (valid) {
            this.listLoading = true;
            var formData = new FormData();
            formData.append("atunitprice", this.ruleFormValue.atunitprice);
            formData.append("companyId", this.companyId);
            formData.append("createTime", new Date());
            formData.append("createUserId", Auth().user().attrs.id);
           // formData.append("file", "");
            formData.append("grade", this.ruleFormValue.grade);
            formData.append("id", 0);
            formData.append("productArea", this.ruleFormValue.productArea);
            formData.append("productName", this.ruleFormValue.productName);
            formData.append("productProfile", this.ruleFormValue.productProfile);
            formData.append("specification", this.ruleFormValue.specification);
            formData.append("updateTime", new Date());
            formData.append("updateUserId", Auth().user().attrs.id);
            formData.append("variety", this.ruleFormValue.variety);

          Request()
            .post("/api/product_business/create", formData)
            .then(response => {
              setTimeout(() => { this.listLoading = false; }, 0.01 * 1000);
              this.$router.go(-1);
            })
            .catch(error => {});
        } else {
          console.log(" !!");
          return false;
        }
      });
    }
  }
};
</script>