<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="box">
      <div class="iptBox">
        <div class="select_label"></div>
        <el-select v-model="currTown" placeholder="" @change="getList()">
          <el-option v-for="item in township" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
        <div class="select_label"></div>
        <el-radio v-model="bTypes" label="1" v-on:change="getList(true)"></el-radio>
        <el-radio v-model="bTypes" label="2" v-on:change="getList(true)"></el-radio>
        <div class="select_label"></div>
        <el-select v-model="public_license" placeholder="" @change="onSelectPubLicense">
          <el-option
            v-for="item in publicOptions"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
        <div class="select_label"></div>
        <el-select v-model="public_punish" placeholder="" @change="onSelectPubPunish">
          <el-option
            v-for="item in publicOptions"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
        <div class="select_label" v-if="loggedinUserType !== 3">
          <el-button type="outline-primary" v-on:click="getList()" disabled></el-button>
        </div>
      </div>

      <el-container>
        <el-table
          :data="tableData"
          v-loading="listLoading"
          fit
          style="width: 100%;"
          :row-class-name="rowIndex"
          highlight-current-row
        >
          <el-table-column :formatter="order" label="" width="70"></el-table-column>
          <el-table-column
            label=""
            width="150"
            v-if="loggedinUserType ===2 || loggedinUserType === 0"
          >
            <template slot-scope="{row}">{{filterCompnay(row.creditCode)}}</template>
          </el-table-column>
          <el-table-column label="" width="150" v-if="loggedinUserType !==2">
            <template slot-scope="{row}">{{filterCompnay(row.creditCode)}}</template>
          </el-table-column>
          <el-table-column
            prop="creditCode"
            label=""
            v-if="loggedinUserType ===2 || loggedinUserType === 0"
          ></el-table-column>
          <el-table-column prop="creditCode" label="" v-if="loggedinUserType !==2"></el-table-column>
          <el-table-column prop="public_license" label="">
            <template slot-scope="{row}">
              <el-button
                v-if="row.public_license > 0"
                v-on:click="$router.push({path: `/corporateCreditFile/adminLicenseInfo`,query: {creditCode:row.creditCode}})"
              ></el-button>
              <el-button
                v-else
                disabled
              >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="public_punish" label="">
            <template slot-scope="{row}">
              <el-button
                v-if="row.public_punish > 0"
                v-on:click="$router.push({path: `/corporateCreditFile/adminPenaltyInfo`,query: {creditCode:row.creditCode}})"
              ></el-button>
              <el-button
                v-else
                disabled
              >&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="{row}">
              <span
                class="rating-action"
                v-on:click="$router.push({path: `/corporateCreditFile/ratingInfo`,query: {creditCode:row.creditCode}})"
              >{{row.nowGrade}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="200">
            <template slot-scope="{row}">
              <el-button
                v-on:click="$router.push({path: `/corporateCreditFile/threeProduction`,query: {creditCode:row.creditCode}})"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-container>
      <div class="pageBox">
        <pagination
          v-show="total > 0"
          :total="total"
          :page.sync="page.pageIndex"
          :limit.sync="page.pageSize"
          @pagination="getList"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from "@/components/common/pagination";
import Request from "../../services/api/request.js";
import Auth from "@/services/authentication/auth.js";

export default {
  name: "creditRating",
  components: { Pagination },
  data() {
    return {
      township: [{ id: 0, name: "" }],
      publicOptions: [
        { id: 0, name: "" },
        { id: 1, name: "" },
        { id: 2, name: "" }
      ],
      currTown: 0,
      public_license: 0,
      public_punish: 0,
      page: {
        pageIndex: 1,
        pageSize: 20
      },
      listLoading: true,
      bTypes: 0,
      status: 0,
      total: 0,
      tableData: [],
      companyProduction: [],
      gradData: [],
      loggedinUserType: null
    };
  },
  mounted() {
    this.getTown();
    this.getList();
    this.getCompanyProduction();
  },
  created() {
    this.loggedinUserType = Auth().user().attrs.userType;
  },
  methods: {
    rowIndex({ row, rowIndex }) {
      row.rowIndex = rowIndex;
    },
    onSelectPubLicense() {
      this.getList();
    },
    onSelectPubPunish() {
      this.getList();
    },
    getCompanyProduction() {
      Request()
        .get("/api/company_production/name")
        .then(response => {
          this.companyProduction = response;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getTown() {
      Request()
        .get("/api/town/all")
        .then(response => {
          this.township = this.township.concat(response);
        })
        .catch(error => {
          console.log(error);
        });
    },
    /* eslint-disable */
    getList(update = false) {
      this.listLoading = true;
      Request()
        .get("/api/company_production/all", {
          companyType: this.bTypes,
          approvalStatus: this.status - 1,
          pageNo: this.page.pageIndex - 1,
          pageSize: this.page.pageSize,
          townId: this.currTown
        })
        .then(response => {
          var tmpData = response.data;
          if (this.public_license == 1) {
            tmpData = tmpData.filter(function(licesnse) {
              return licesnse.public_license > 0;
            });
          } else if (this.public_license == 2) {
            tmpData = tmpData.filter(function(licesnse) {
              return licesnse.public_license == 0;
            });
          }
          if (this.public_punish == 1) {
            tmpData = tmpData.filter(function(punish) {
              return punish.public_punish > 0;
            });
          } else if (this.public_punish == 2) {
            tmpData = tmpData.filter(function(punish) {
              return punish.public_punish == 0;
            });
          }
          this.tableData = [];
          let indexItem = 0;
          tmpData.map(item => {
            let gradeArrayName = "credit_grade_data_" + indexItem;
            this.getNowGrade(response[gradeArrayName]).then(res => {
              item.nowGrade = this.getGradeString(res);
              this.tableData.push(item);
              this.total = this.tableData.length;
            });
            indexItem++;
          });
          this.total = this.tableData.length;
          setTimeout(() => {
            this.listLoading = false;
          }, 0.5 * 1000);
        })
        .catch(error => {
          console.log(error);
          this.tableData = [];
          this.srcData = [];
          this.total = 0;
          setTimeout(() => {
            this.listLoading = false;
          }, 0.5 * 1000);
        });
    },
    /* eslint-enable */
    filterCompnay(credit) {
      let company = this.companyProduction.find(x => x.creditCode === credit);
      if (company) {
        return company.companyName;
      } else {
        return "";
      }
    },
    async getNowGrade(creditCode, gradeArray) {
      let nowGrade = "";
      if (!gradeArray || !gradeArray.length) {
        nowGrade = "";
      } else {
        nowGrade = gradeArray.pop().nowGrade;
      } 
      return nowGrade;
    },
    // getGrade(dataTable) {
    //   let strGrade = "";
    //   Request()
    //     .get("/api/company_credit_grade/all", {
    //       approvalStatus: this.status - 1,
    //       creditCode: dataTable.creditCode,
    //       pageNo: this.page.pageIndex - 1,
    //       pageSize: this.page.pageSize,
    //       townId: this.currTown
    //     })
    //     .then(response => {
    //       this.gradData = response;
    //       let nSIze = this.gradData.length;
    //       strGrade = this.getGradeString(this.gradData[nSIze - 1].nowGrade);
    //       dataTable.nowGrade = strGrade;
    //     })
    //     .catch(error => {
    //       console.log(error);
    //       return "";
    //     });

    //   return dataTable.nowGrade;
    // },
    getGradeString(grade) {
      let strGrade = "";
      switch (grade) {
        case "A":
          strGrade = "A";
          break;
        case "B":
          strGrade = "B";
          break;
        case "C":
          strGrade = "C";
          break;
        default:
          strGrade = "A";
      }
      return strGrade;
    },
    order(row) {
      return this.page.pageSize * (this.page.pageIndex - 1) + row.rowIndex + 1;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "./corporateCreditFile.scss";
</style>