<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="box">
      <el-form 
        ref="addForm" 
        :model="addForm" 
        :rules="rules" 
        label-width="100px"
        class="form-width" 
        v-loading="listLoading"
    >
        <el-form-item prop="timingRelease" label="" class="input-width label-align">
            <el-input 
                v-model="addForm.timingRelease" 
                auto-complete="off"
            >
            </el-input>
        </el-form-item>
        <el-form-item label="" prop="emergencyDegree" class="input-width label-align">
            <el-select 
                v-model="addForm.emergencyDegree" 
                placeholder=""
            >
                <el-option
                    v-for="item in emergencyDegrees"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                >
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="type" class="input-width label-align">
            <el-input 
                v-model="addForm.type" 
                auto-complete="off"
            >
            </el-input>
        </el-form-item>
        <el-form-item label="" prop="releaseTime" class="input-width label-align">
            <el-date-picker 
                type="date" 
                v-model="addForm.releaseTime" 
            >
            </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="releasePerson" class="input-width label-align">
            <el-input 
                v-model="addForm.releasePerson" 
                auto-complete="off"
            >
            </el-input>
        </el-form-item>
        <el-form-item prop="title" label="" class="label-align">
            <el-input 
                v-model="addForm.title" 
                auto-complete="off"
            >
            </el-input>
        </el-form-item>
        <el-form-item prop="content" label="" class="label-align">
            <el-input 
                v-model="addForm.content" 
                auto-complete="off"
                type="textarea"
                :rows="10"
            >
            </el-input>
        </el-form-item>
        <el-form-item label="" prop="file">
            <input 
                type="file" 
                id="file" 
                style="display: none" 
                ref="file_live_1"
                v-on:change="handleFileUpload()"
            />
            <el-button plain @click="chooseFile()"></el-button>
            <span style="width: 300px;">{{ fileName }}</span>
        </el-form-item>
        <el-form-item>
            <el-button type="success" plain @click="onSubmit('addForm')">
                
            </el-button>
            <el-button type="danger" plain v-on:click="$router.go(-1)">
                
            </el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import Request from "../../../services/api/request.js";
import { Urls } from "../../../services/constants";

export default {
  data() {
    return {
        addForm: {
            timingRelease: "",
            emergencyDegree: 0,
            type: "",
            releaseTime: "",
            releasePerson: "",
            title: "",
            content: "",
        },
        emergencyDegrees: [
            { id: 0, name: "" },
            { id: 1, name: "" },
            { id: 2, name: "" }
        ],
        rules: {
            content: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            releaseTime: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            title: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            timingRelease: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            emergencyDegree: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            type: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            releasePerson: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
        },
        images:[],
        fileName: '',
        file_live_1: null

    };
  },
  mounted() {
  },
  methods: {
    onSubmit(formName) {
        this.$refs[formName].validate(valid => {
            if (valid) {
                this.dataloading = true;
                var formData = new FormData();
                formData = this.makeFormData();
                Request()
                    .post("/api/notice/create", formData)
                    .then(response => {
                        this.$router.push({ path: "/notice" });
                        setTimeout(() => {
                            this.dataloading = false;
                        }, 0.01 * 1000);
                    })
                    .catch(error => {});
            }
        });
    },
    chooseFile() {
      document.getElementById('file').click()
    },
    handleFileUpload() {
        this.file_live_1 = this.$refs.file_live_1.files[0];
        this.fileName = this.file_live_1.name;
    },
    makeFormData() {
        var mainFormData = new FormData();
        mainFormData.append("timingRelease", this.addForm.timingRelease);
        mainFormData.append("emergencyDegree", this.addForm.emergencyDegree);
        mainFormData.append("type", this.addForm.type);
        mainFormData.append("releaseTime", this.addForm.releaseTime);
        mainFormData.append("releasePerson", this.addForm.releasePerson);
        mainFormData.append("title", this.addForm.title);
        mainFormData.append("content", this.addForm.content);
        mainFormData.append("id", 0);
        mainFormData.append("file", this.file_live_1);
        return mainFormData;
    },
  }
};
</script>

<style lang="scss">
</style>