<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="box">
      <div class="iptBox" v-if="loggedinUserType !== 3">
        <div class="select_label no-margin-left"></div>
        <el-select placeholder v-model="currTown" @change="getList">
          <el-option v-for="town in township" :key="town.id" :label="town.name" :value="town.id"></el-option>
        </el-select>
        <div class="select_label"></div>
        <el-select v-model="status" placeholder="" @change="getList">
          <el-option v-for="(item, index) in appStatus" :key="item" :label="item" :value="index"></el-option>
        </el-select>
        <div class="select_label">
          <el-button disabled type="primary" plain></el-button>
        </div>
      </div>

      <el-container>
        <el-table
          :data="tableData"
          v-loading="listLoading"
          fit
          style="width: 100%;"
          :row-class-name="rowIndex"
          highlight-current-row
        >
          <el-table-column :formatter="order" label="" width="70"></el-table-column>
          <el-table-column label="" width="150">
            <template slot-scope="{ row }">
              {{
              filterCompnay(row.creditCode)
              }}
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="{ row }">
              {{
              getGradeString(row.originalGrade)
              }}
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="{ row }">
              {{
              getGradeString(row.nowGrade)
              }}
            </template>
          </el-table-column>
          <el-table-column prop="gradeTime" label="">
            <template slot-scope="{ row }">
              {{
              row.gradeTime | formatDate
              }}
            </template>
          </el-table-column>
          <el-table-column prop="creditAvailableStart" label="">
            <template slot-scope="{ row }">
              {{ row.creditAvailableStart | formatDate }}{{
              row.creditAvailableEnd | formatDate
              }}
            </template>
          </el-table-column>
          <el-table-column prop="gradeUnit" label=""></el-table-column>
          <!-- <el-table-column prop="approvalStatus" label="" width="100"></el-table-column> -->
          <el-table-column label="" width="100">
            <template slot-scope="{ row }">
              {{
              getStatus(row.approvalStatus)
              }}
            </template>
          </el-table-column>
          <el-table-column label="" v-if="loggedinUserType !== 3" class-name="text-center">
            <template slot-scope="{ row }" v-if="loggedinUserType === 1 || loggedinUserType === 0">
              <el-button
                v-on:click="$router.push(`/creditRating/${row.creditGradeId}`)"
                plain
                type="success"
              ></el-button>
            </template>
            <template slot-scope="{ row }">
              <el-button
                plain
                type="success"
                v-if="loggedinUserType === 2 || loggedinUserType === 0"
                v-on:click="
                  $router.push({
                    path: `/creditRating/edit/${row.creditGradeId}`,
                    query: { company: filterCompnay(row.creditCode) }
                  })"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-container>
      <div class="pageBox">
        <pagination
          v-show="total > 0"
          :total="total"
          :page.sync="page.pageIndex"
          :limit.sync="page.pageSize"
          @pagination="getList"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from "@/components/common/pagination";
import Request from "../../services/api/request.js";
import Auth from "@/services/authentication/auth.js";

export default {
  name: "creditRating",
  components: { Pagination },
  data() {
    return {
      township: [{ id: 0, name: "" }],
      currTown: 0,
      appStatus: ["", "", "", ""],
      status: 0,
      page: {
        pageIndex: 1,
        pageSize: 20
      },
      companyProduction: [],
      listLoading: true,
      total: 100,
      tableData: [],
      loggedinUserType: null
    };
  },
  created() {
    this.getTown();
    this.getList();
    this.getCompanyProduction();
    this.loggedinUserType = Auth().user().attrs.userType;
  },
  methods: {
    rowIndex({ row, rowIndex }) {
      row.rowIndex = rowIndex;
    },
    getCompanyProduction() {
      Request()
        .get("/api/company_production/name")
        .then(response => {
          this.companyProduction = response;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getTown() {
      Request()
        .get("/api/town/all")
        .then(response => {
          this.township = this.township.concat(response);
        })
        .catch(error => {
          console.log(error);
        });
    },
    getList() {
      this.listLoading = true;
      Request()
        .get("/api/company_credit_grade/all", {
          approvalStatus: this.status - 1,
          pageNo: this.page.pageIndex - 1,
          pageSize: this.page.pageSize,
          townId: this.currTown
        })
        .then(response => {
          this.tableData = response.data;
          this.total = response.total;
          setTimeout(() => {
            this.listLoading = false;
          }, 0.5 * 1000);
        })
        .catch(error => {
          console.log(error);
        });
    },
    getStatus(status) {
      let changedStatus = parseInt(status);
      return this.appStatus[changedStatus + 1];
    },
    filterCompnay(credit) {
      let company = this.companyProduction.find(x => x.creditCode === credit);
      if (company) {
        return company.companyName;
      } else {
        return "";
      }
    },
    getGradeString(grade) {
      let strGrade = "";
      switch (grade) {
        case "A":
          strGrade = "A";
          break;
        case "B":
          strGrade = "B";
          break;
        case "C":
          strGrade = "C";
          break;
        default:
          strGrade = "A";
      }
      return strGrade;
    },
    order(row) {
      return this.page.pageSize * (this.page.pageIndex - 1) + row.rowIndex + 1;
    }
  }
};
</script>
