<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="box">
      <el-form 
        ref="editForm" 
        :model="editForm" 
        :rules="rules" 
        label-width="100px"
        class="form-width" 
        v-loading="dataloading"
    >
        <el-form-item prop="timingRelease" label="" class="input-width label-align">
            <el-input 
                v-model="editForm.timingRelease" 
                auto-complete="off"
            >
            </el-input>
        </el-form-item>
        <el-form-item label="" prop="emergencyDegree" class="input-width label-align">
            <el-select 
                v-model="editForm.emergencyDegree" 
                placeholder=""
            >
                <el-option
                    v-for="item in emergencyDegrees"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                >
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="type" class="input-width label-align">
            <el-input 
                v-model="editForm.type" 
                auto-complete="off"
            >
            </el-input>
        </el-form-item>
        <el-form-item label="" prop="releaseTime" class="input-width label-align">
            <el-date-picker 
                type="date" 
                v-model="editForm.releaseTime" 
            >
            </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="releasePerson" class="input-width label-align">
            <el-input 
                v-model="editForm.releasePerson" 
                auto-complete="off"
            >
            </el-input>
        </el-form-item>
        <el-form-item prop="title" label="" class="label-align">
            <el-input 
                v-model="editForm.title" 
                auto-complete="off"
            >
            </el-input>
        </el-form-item>
        <el-form-item prop="content" label="" class="label-align">
            <el-input 
                v-model="editForm.content" 
                auto-complete="off"
                type="textarea"
                :rows="10"
            >
            </el-input>
        </el-form-item>
        <el-form-item label="" prop="file">
            <input 
                type="file" 
                id="file" 
                style="display: none" 
                ref="file_live_1"
                v-on:change="handleFileUpload()"
            />
            <el-button plain @click="chooseFile()"></el-button>
            <span class="item-value" v-if="!file_live_1">
                <el-link @click="downloadFile()">
                    {{ fileName }}
                </el-link>
            </span>
            <span class="item-value" v-if="file_live_1">
                ({{ fileName }})
            </span>
        </el-form-item>
        <el-form-item>
            <el-button type="success" plain @click="onSubmit('editForm')">
                
            </el-button>
            <el-button type="danger" plain v-on:click="$router.go(-1)">
                
            </el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import Request from "../../../services/api/request.js";
import { Urls } from "../../../services/constants";
import axios from "axios";
export default {
  data() {
    return {
        editForm: {
            timingRelease: "",
            emergencyDegree: 2,
            type: "",
            releaseTime: "",
            releasePerson: "",
            title: "",
            content: "",
        },
        emergencyDegrees: [
            { id: "0", name: "" },
            { id: "1", name: "" },
            { id: "2", name: "" }
        ],
        rules: {
            content: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            releaseTime: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            title: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            timingRelease: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            emergencyDegree: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            type: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
            releasePerson: [
            {
                required: true,
                message: "",
                trigger: "change"
            }],
        },
        images:[],
        fileName: '',
        file_live_1: null,
        id: 0,
        dataloading: true
    };
  },
  beforeMount() {
    this.id = this.$route.params.id;
    this.getData(this.$route.params.id);
  },
  methods: {
    getData(id) {
        this.dataloading = true;
        Request()
            .get("/api/notice/get/" + id)
            .then(response => {
                this.editForm = response;
                this.file_live_1 = response.noticeProfiles;
                this.fileName = this.file_live_1.replace("/uploads/", "");
                setTimeout(() => {
                    this.dataloading = false;
                }, 0.01 * 1000);
            })
            .catch(error => {
                console.log(error);
            });
    },
    downloadFile() {
        axios({
            url: Urls.DOWNLOAD_URL() + this.file_live_1,
            method: "GET",
            responseType: "blob" // important
        }).then(response => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement("a");
            link.href = url;
            link.setAttribute(
                "download",
                this.file_live_1.replace("/uploads/", "")
            ); //or any other extension
            document.body.appendChild(link);
            link.click();
            link.remove();
        });
    },
    onSubmit(formName) {
        this.$refs[formName].validate(valid => {
            if (valid) {
                this.dataloading = true;
                var formData = new FormData();
                formData = this.makeFormData();
                Request()
                    .put("/api/notice/update/" + this.id, formData)
                    .then(response => {
                        this.$router.push({ path: "/notice" });
                        setTimeout(() => {
                            this.dataloading = false;
                        }, 0.01 * 1000);
                    })
                    .catch(error => {});
            }
        });
    },
    chooseFile() {
      document.getElementById('file').click()
    },
    handleFileUpload() {
        this.file_live_1 = this.$refs.file_live_1.files[0];
        this.fileName = this.file_live_1.name;
        
    },
    makeFormData() {
        var mainFormData = new FormData();
        mainFormData.append("timingRelease", this.editForm.timingRelease);
        mainFormData.append("emergencyDegree", this.editForm.emergencyDegree);
        mainFormData.append("type", this.editForm.type);
        mainFormData.append("releaseTime", new Date(this.editForm.releaseTime));
        mainFormData.append("releasePerson", this.editForm.releasePerson);
        mainFormData.append("title", this.editForm.title);
        mainFormData.append("content", this.editForm.content);
        mainFormData.append("noticeProfiles", this.file_live_1);
        mainFormData.append("id", this.id);
        return mainFormData;
    },
  }
};
</script>

<style lang="scss">
</style>