<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/sampleCheck' }"></el-breadcrumb-item>
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="box">
      <el-form v-if="data" :rules="dataRulse" ref="data" :model="data" label-width="100px">
        <el-form-item label=":" prop="sampleName">
          <el-input v-model="data.sampleName" style="width: 300px;" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="sampleTime">
          <el-date-picker v-model="data.sampleTime" style="width: 300px !important;" placeholder=""></el-date-picker>
        </el-form-item>
        <el-form-item label=":" prop="checkPerson">
          <el-input v-model="data.checkPerson" style="width: 300px;" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" prop="checkResult">
          <el-select v-model="data.checkResult" placeholder="" style="width:300px !important;">
            <el-option v-for="item in [{value:0, label:''}, {value: 1, label:''}]"
              :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" prop="checkUnit">
          <el-input v-model="data.checkUnit" style="width: 300px;" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="checkFiles" label="">
          <div style="display: flex;">
            <div class="item-label">
              <input type="file" id="file" style="display: none" ref="file"
                v-on:change="handleFileUpload()"/>
              <el-button plain @click="chooseFile()"></el-button>
            </div>
            <span class="item-value" style="width: 500px;">{{data.checkFiles}}</span>
          </div>
        </el-form-item>
        <el-form-item label="">
            <el-button type="success" plain @click="onSubmit()"></el-button>
            <el-button type="danger" plain @click="$router.go(-1)"></el-button>
        </el-form-item>
      </el-form>
      <template v-if="!data">No matching data!</template>
    </div>
  </div>
</template>

<script>
import Request from '@/services/api/request'
export default {
  name: "addSampleCheckResult",
  data() {
    return {
      id: -1,
      file: null,
      data: {
        checkFiles: "",
        checkPerson: "",
        checkResult: '',
        checkUnit: '',
        createUserId: 0,
        id: 0,
        sampleId: 0,
        sampleName: "",
        sampleTime:"",
        updateUserId: 0
      },
      dataRulse: {
        sampleName: [{required: true, message: '', trigger: 'blur,change'}],
        sampleTime: [{required: true, message: '', trigger: 'blur,change'}],
        checkPerson: [{required: true, message: '', trigger: 'blur,change'}],
        checkFiles: [{required: true, message: '', trigger: 'blur,change'}],
        checkResult: [{required: true, message: '', trigger: 'blur,change'}],
        checkUnit: [{required: true, message: '', trigger: 'blur,change'}],
      },
    };
  },
  methods: {
    chooseFile() {
      this.$refs.file.click();
    },

    handleFileUpload() {
      this.file = this.$refs.file.files[0];
      this.data.checkFiles = this.file.name;
    },
    onSubmit() {
      let formData = new FormData();
      formData.append("checkFiles", this.data.checkFiles);
      formData.append("checkPerson", this.data.checkPerson);
      formData.append("createUserId", this.data.createUserId);
      formData.append("id", this.data.id);
      formData.append("sampleId", this.data.sampleId);
      formData.append("sampleName", this.data.sampleName);
      formData.append("updateUserId", this.data.updateUserId);
      formData.append("file", this.file);
      formData.append("sampleTime", this.data.sampleTime);
      formData.append("checkResult", this.data.checkResult);
      formData.append("checkUnit", this.data.checkUnit);
      formData.append("createTime", this.data.sampleTime); // not sure
      formData.append("updateTime", this.data.sampleTime); // not sure

      this.$refs.data.validate((valid) => {
        if (valid) {
          Request()
            .post("/api/sample_check_result/create", formData)
            .then(response => {
              this.$router.push(`/sampleCheck`);
            })
            .catch(error => {
              console.error(error);
            });
        }
      });
    },
    getDateString(dt) {
      const date = new Date(dt);
      return date.toLocaleDateString();
    },
  },
};
</script>
<style lang="scss" scoped>
@import "./addSampleCheckResult.scss";
</style>
