<template>
  <div class="container">
    <div class="title">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/productionSubject' }"
          ></el-breadcrumb-item
        >
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item class="actived"></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="box">
      <el-form
        ref="ruleForm"
        :model="ruleFormValue"
        :rules="rules"
        label-width="100px"
        v-loading="listLoading"
      >
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="name">
              <el-input v-model="ruleFormValue.name" ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="" prop="sort">
              <el-input v-model="ruleFormValue.sort" ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item>
          <el-button type="success" @click="onSubmit('ruleForm')" plain
            ></el-button
          >
          <el-button type="danger" @click="goBack" plain></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import Request from "../../../../../../../services/api/request.js";
export default {
  name: "editCustomProductProperty",
  data() {
    return {
      listLoading: false,
      ruleFormValue: {
        id: -1,
        name: "",
        sort: ""      
      },
      rules: {
        name: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        sort: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      }
    };
  },
  created() {    
    this.ruleFormValue.id = this.$route.query.id;
    this.ruleFormValue.name = this.$route.query.name;
    this.ruleFormValue.sort = this.$route.query.sort;
  },
  methods: {
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var formData = {
            "id": 0,
            "name": this.ruleFormValue.name,
            "sort": this.ruleFormValue.sort
          }
          this.listLoading = true;
          Request()
            .put("/api/product_property_option/update/" + this.ruleFormValue.id, formData)
            .then(response => {
              setTimeout(() => {
                this.listLoading = false;          
              }, 0.5*100);
              this.$router.push({
                path: `/productionSubject/mainProduct/productProperty/editProductProperty/customProductProperty/${this.ruleFormValue.id}`
              });
            })
            .catch(error => {});
        } else {
          console.log(" !!");
          return false;
        }
      });
    },
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped>
</style>
